import * as React from 'react';
import { useState } from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import Button from '@mui/material/Button';
import MenuIcon from '@mui/icons-material/Menu';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import HomeIcon from '@mui/icons-material/Home';
import Box from '@mui/material/Box';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import AccountCircle from '@mui/icons-material/AccountCircle';
import Footer from './Footer';
import { Link, navigate } from 'gatsby';
import useAuth from "../hooks/userAuth";

// Category icons
import CodeIcon from '@mui/icons-material/Code';
import SchoolIcon from '@mui/icons-material/School';
import WorkIcon from '@mui/icons-material/Work';
import MovieIcon from '@mui/icons-material/Movie';
import GroupsIcon from '@mui/icons-material/Groups';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import FitnessCenterIcon from '@mui/icons-material/FitnessCenter';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import AppsIcon from '@mui/icons-material/Apps';

const categories = [
  { name: 'Developer Tools', icon: <CodeIcon /> },
  { name: 'Education', icon: <SchoolIcon /> },
  { name: 'Productivity', icon: <WorkIcon /> },
  { name: 'Entertainment', icon: <MovieIcon /> },
  { name: 'Social Media', icon: <GroupsIcon /> },
  { name: 'E-commerce', icon: <ShoppingCartIcon /> },
  { name: 'Health & Fitness', icon: <FitnessCenterIcon /> },
  { name: 'Finance', icon: <AttachMoneyIcon /> },
  { name: 'Other', icon: <AppsIcon /> }
];

const Layout = ({ children, deployButton, promoteButton }) => {
  const { isLogin, logout } = useAuth();
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const menuOpen = Boolean(anchorEl);

  const toggleDrawer = (open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }
    setDrawerOpen(open);
  };

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    setAnchorEl(null);
    logout();
    console.log('User logged out');
  };

  const drawerContent = (
    <Box
      sx={{ width: 250 }}
      role="presentation"
      onClick={toggleDrawer(false)}
      onKeyDown={toggleDrawer(false)}
    >
      <Box sx={{ display: 'flex', justifyContent: 'flex-end', p: 2 }}>
        <IconButton onClick={toggleDrawer(false)}>
          <MenuIcon />
        </IconButton>
      </Box>
      <List>
        <ListItem button component={Link} to="/">
          <ListItemIcon>
            <HomeIcon />
          </ListItemIcon>
          <ListItemText primary="Home" />
        </ListItem>
        
        {categories.map((category) => (
          <ListItem 
            key={category.name} 
            button 
            component={Link} 
            to={`/apps/${category.name}`}
          >
            <ListItemIcon>
              {category.icon}
            </ListItemIcon>
            <ListItemText primary={category.name} />
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={toggleDrawer(true)}
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>

          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            <Link
              to="/"
              style={{ color: 'white', textDecoration: 'none' }}
              activeStyle={{ color: 'white' }}
            >
              AppSeek
            </Link>
          </Typography>

          {deployButton && (
            <Box sx={{ display: { xs: 'none', sm: 'block' }, mr: 2 }}>
              {deployButton}
            </Box>
          )}

          {promoteButton && (
            <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
              {promoteButton}
            </Box>
          )}

          <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
            {!isLogin && (
              <Button variant="contained" onClick={() => navigate('/login')}>
                Login
              </Button>
            )}
            {isLogin && (
              <>
                <IconButton
                  size="large"
                  edge="end"
                  aria-label="account of current user"
                  aria-controls="menu-appbar"
                  aria-haspopup="true"
                  onClick={handleMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>
                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  keepMounted
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={menuOpen}
                  onClose={handleClose}
                >
                  <MenuItem disabled>{isLogin.username}</MenuItem>
                  <MenuItem onClick={handleLogout}>Logout</MenuItem>
                </Menu>
              </>
            )}
          </Box>
        </Toolbar>
      </AppBar>

      <Drawer anchor="left" open={drawerOpen} onClose={toggleDrawer(false)}>
        {drawerContent}
      </Drawer>

      <Box component="main" sx={{ flexGrow: 1, p: 3, mt: 8 }}>
        {children}
      </Box>

      <Footer />
    </Box>
  );
};

export default Layout;